<div class="home"><div class="logo" @dblclick="showCheat = !showCheat">Duetto</div><div class="game-choooser" v-if="hasToStartGame"><GameChooser @start="startGame"></GameChooser></div><div class="main-content" v-else><div class="menu"><button @click="resetGame">New game</button><div class="note" v-if="player === currentPlayer &amp;&amp; !hasToChooseFirstPlayer">It's your turn!</div><div class="turn-section" v-if="!hasToChooseFirstPlayer"><div class="player" :class="{current: currentPlayer === 'A'}">A </div><div class="turns"><div class="turn" v-for="i in turnPass.A">✓</div></div><div class="player" :class="{current: currentPlayer === 'B'}">B</div><div class="turns"><div class="turn" v-for="i in turnPass.B">✓</div></div><div class="remaining-turns"><div class="turn" v-for="i in remainingSuggestions">👤</div></div></div><div class="cheat" v-if="showCheat"><label><input type="checkbox" v-model="cheat">Cheat</label></div><button v-if="!readOnly &amp;&amp; !passDisabled" @click="pass">Pass</button><div class="you" v-if="hasToChooseFirstPlayer">You are: {{player}}</div><div class="choose-first-player" v-if="hasToChooseFirstPlayer"><button v-for="p in ['A','B']" @click="select(p)">{{p}}</button></div><div class="game-status" v-if="gameOver">:(</div><div class="game-status" v-if="gameWon">Yeeeeeee</div></div><GameGrid :words="words" :player="showForPlayer" :forceReadOnly="readOnly"></GameGrid></div></div>