<div class="word-card" :class="[{['read-only']: readOnly}, wordCardClass]" @dblclick="click"><label class="text">{{word}}</label><div class="indicator" :class="{[cardType]: cardType}"></div><div class="choices">   <div class="brown" v-for="dir in brownDirections" :key="dir"> <label>{{dir}}</label></div><div class="green" v-if="greenDirection"><label>{{greenDirection}}</label></div></div></div>