<form class="game-chooser" @submit.prevent="start"><label>Password</label><input type="text" v-model="password"><label>Player</label><div class="player-choices"><label><input type="radio" value="A" v-model="player">A</label><label><input type="radio" value="B" v-model="player">B</label></div><label>Suggestions</label><input type="number" v-model="suggestions"><label>Language</label><div class="language-choices"><label><input type="radio" value="italian" v-model="language">Italiano<input type="radio" value="english" v-model="language">English</label></div><div class="start"><input type="submit" :disabled="!canStart" value="Start"></div></form>